/* Minimap styles */

.minimap {
  height: auto;
  position: fixed;
  z-index: 15;
  transition: 0.4s linear;
  transform: scale(0.20) translate(-300%, -4000px);
  max-height: 3000px;
  overflow-y: scroll;
}

.minimap::-webkit-scrollbar {
  width: 50px;
}

.minimap::-webkit-scrollbar-thumb {
  display: block;
  cursor: pointer;
}

.minimap_active {
  transform: scale(0.20) translate(-180%, -3600px);
}

#minimapButton {
  display: grid;
  position: fixed;
  transition: 0.2s linear;
  width: 6%;
  height: 30px;
  background: #8FBCBB;
  border-radius: 5px;
  place-items: center;
  color: #e5e9f0;
  font-weight: 700;
  cursor: pointer;
  top: 90px;
  left: 3%;
  user-select: none;
}

#minimapCurtain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #a3be8c;
  opacity: 0.5;
}

.minimapButton_active {
  background: #4c566a !important;
}

@media (max-width: 1280px) {
  #minimap {
    display: none;
  }
  #minimapButton {
    display: none;
  }
}
